import { CustomControlledAutocomplete } from 'Components';
import { useEffect, useState } from 'react';
import { useDebounce } from 'Hooks';
import { useWatch } from 'react-hook-form';

interface IStreetPicker {
    disabled?: boolean;
    fieldName: string;
    fieldId: string;
    cityIdFieldName: string;
    onUpdateStreet: () => void;
    isDividedField: string;
}

export const StreetPicker = ({
    disabled,
    fieldId,
    fieldName,
    cityIdFieldName,
    onUpdateStreet,
    isDividedField,
}: IStreetPicker) => {
    const [streets, setStreets] = useState<any[] | undefined>();
    const [isDivided, setIsDivided] = useState(false);
    const cityId = useWatch({ name: cityIdFieldName });
    const cityDivided = useWatch({ name: isDividedField });

    const StreetChangeHandler = useDebounce((val: string) => {
        if (val.length > 1) {
            onUpdateStreet();
            setStreetsOption(cityId, val);
        } else if (streets?.length) {
            setStreets(undefined);
            onUpdateStreet();
        }
    }, 350);

    const setStreetsOption = async (id?: string, text?: string) => {
        //TODO
        setStreets(streetsListTemp.slice(0,Math.floor(Math.random() * streetsListTemp.length-2) + 1))
        // if (id && text) {
        //     const result = await getStreets({
        //         CityID: id,
        //         CityName: '',
        //         SearchMode: 'ID-StartsWith',
        //         StartsWith: text,
        //     });
        //     if (result) setStreets(result);
        // } else {
        //     if (streets != undefined) setStreets(undefined);
        // }
    };

    useEffect(() => {
        setIsDivided(cityDivided ? true : false);
    }, [cityDivided]);

    useEffect(() => {
        setStreets(undefined);
    }, [cityId]);

    return (
        <CustomControlledAutocomplete
            important={isDivided}
            name={fieldName}
            nameId={fieldId}
            onChange={StreetChangeHandler}
            options={streets}
            changeableOptions
            label={"Dictionary.street.he"}
            disabled={disabled}
        />
    );
};



